import InternetPackage from './InternetPackage';


import amc from '../../images/checkout/amc_logo.png';
import cinemax1 from '../../images/checkout/cinemax.png';
import cinemax2 from '../../images/checkout/cinemax2.png';
import hbo from '../../images/checkout/hbo.png';
import hbo2 from '../../images/checkout/hbo2.png';
import hbo3 from '../../images/checkout/hbo3.png';
import diema1 from '../../images/checkout/diema1.png';
import diema2 from '../../images/checkout/diema2.png';
import trace from '../../images/checkout/trace.png';

class TvExtra {
  constructor(name, description, icons, price) {
    this.name = name;
    this.description = description;
    this.icons = icons.slice();
    this.price = price;
  }
}

class TvPackage extends InternetPackage {
  extras = [
    { extra: new TvExtra('Cinemax & AMC', 'С включените канали Cinemax & AMC във всички пакети на НетСърф, ще гледате холивудски продукции, както и пълни се екшън сериали.', [amc, cinemax1, cinemax2], 0), isAdded: true, isIncluded: true, },
    { extra: new TvExtra('HBO', 'Ти си филмов маниак? Обичаш завладяващите филми и сериали? Потопи се в океана от над 200 сериала, 800 филма и детски предложения, като добавиш HBO към своя пакет. Получаваш и достъп до HBOGO, за да гледаш в движение най-добро от света на филмите и сериалите.', [hbo, hbo2, hbo3], 10.40), isAdded: false, isIncluded: false, },
    { extra: new TvExtra('Diema Xtra', 'Добави Diema Xtra към пакета си и гледай мачове от Шампионска Лига, Лига Европа, английски, френски, немски и български футбол,  както и Формула 1, НБА, Волейбол България, Бокс и много други спортове.', [diema1, diema2, trace], 7.99), isAdded: false, isIncluded: false, },
  ];

  constructor(id, size, speed, price, features, title, hdFetcherInfo, tvCount) {
    super(id, size, speed, price, features);

    this.title = title;
    this.tvChannels = title.split(" ")[0] + "+";
    this.tvChannelsCount = parseInt(title.split(" ")[0]);
    this.hdChannels = Math.floor(parseInt(title.split(" ")[0]) / 3) + "+";
    this.hdFetcher = tvCount;
    this.hdFetcherInfo = "За всеки телевизор е необходим 1 IPTV приемник (STB). След избор на пакет, ще имате възможността да увеличите броя IPTV приемници. IPTV приемникът е крайно устройство, което преобразува и подготвя ТВ сигнала за излъчване на Вашия телевизор.";
    this.isOnlyInternet = false;


    this.tvExtras = this.determineTvExtras(features.includedextras);
  }

  getPriceAccordingToDuration = () => {
    switch (parseInt(this.duration)) {
      case 0:
        return this.originalPrice * 1.5;
      case 1:
        return this.originalPrice * 1.2;
      default:
        return this.originalPrice;
    }
  }

  determineTvExtras = (includedextras) => {
    switch (includedextras) {
      case 0: {
        return this.extras.slice();
      }
      case 1: {
        let newExtras = this.extras.slice();
        newExtras[1] = { ...newExtras[1], isIncluded: true, isAdded: true };
        return newExtras;
      }
      case 12: {
        let newExtras = this.extras.slice();
        newExtras[1] = { ...newExtras[1], isIncluded: true, isAdded: true };
        newExtras[2] = { ...newExtras[2], isIncluded: true, isAdded: true };
        return newExtras;
      }
      default:
        return this.extras.slice();
    }
  }

  setExtraAdded = (index, isAdd) => {
    this.tvExtras[index] = { ...this.tvExtras[index], isAdded: isAdd };
  }

  setTvCount = (tvCount) => this.hdFetcher = tvCount;

  getPriceWithExtras = () => {
    return (this.getPriceAccordingToDuration() + this.getAdditionalTVsPrice(this.hdFetcher) + this.getExtrasPrice());
  }

  getAdditionalTVsPrice = (numberOfTvs) => {
    const tvNum = parseInt(numberOfTvs);
    switch (tvNum) {
      case 1:
        return 0;
      case 2:
      case 3:
        return (tvNum - 1) * 2.5;
      default:
        return (tvNum - 3) * 5 + 5;
    }
  }

  getExtrasPrice = () => {
    let res = 0;
    this.tvExtras.forEach((v) => {
      if (!v.isIncluded && v.isAdded) res += v.extra.price;
    });
    return res;
  };

  toStringTvExtras = () => {
    let res = '';
    this.tvExtras.forEach((v) => {
      if (v.isAdded) {
        res += v.extra.name + " +" + v.extra.price + "; ";
      }
    });
    return res;
  }

  toString = () => {
    return "Пакет: " + this.internalTitle
      + "\n<br>Канали: " + this.title
      + "\n<br>Интернет скорост: " + this.internetSpeed
      + "\n<br>Срок на договор: " + this.duration + "г."
      + "\n<br>Базова цена: " + String(this.originalPrice).split('.')[0] + "." + String(this.originalPrice).split('.')[1].slice(0, 2)
      + "\n<br>Брой ТВ приемници: " + this.hdFetcher
      + "\n<br>Екстри: " + this.toStringTvExtras()
      + "\n<br>Цена на конфигурацията: " + this.getPriceMain() + "." + this.getPriceCoins();
  }
}

Object.setPrototypeOf(TvPackage.prototype, InternetPackage);

export default TvPackage;