import React from 'react';
import styled from 'styled-components';

const PriceTag = ({ price }) => {
  const Wrapper = styled.div`
    background: #F4F5FA;
    border-radius: 6.55046px;
    padding: 0.5rem 2rem 0.5rem 0;
    transform: scale(0.8);
  `;

  let priceString = String(price).split('.');
  priceString[1] = parseInt(priceString[1]) >= 1 && parseInt(priceString[1]) <= 9 ? priceString[1] + '0' : priceString[1];
  return (
    <Wrapper>
      <div className='row d-flex justify-content-center align-items-center'>
        <div className='col-6 row m-0 p-0'>
          <div className='col-6 p-0 d-flex justify-content-end align-items-center'>
            <h3 className='price sm'><span className='text-blue'>{priceString[0]}</span></h3>
          </div>
          <div className='col-6 p-0 m-0 row d-flex justify-content-start '>
            <h3 className='price-coins sm'><span className='text-blue'>{priceString[1]}</span></h3>
            <h3 className='price-label sm pb-1' style={{ whiteSpace: 'nowrap' }}><span className='text-blue'>лв/мес</span></h3>
          </div>
        </div>
      </div>
    </Wrapper>);
};

export const Step2 = ({ dispatch, packageInfo }) => {
  const extras = packageInfo.tvExtras;

  const SelectCard = styled.div`
    white-space: pre-wrap;
    padding: 1.5rem;
    margin: 1rem 1rem 1rem 1rem;
    opacity: ${props => props.isAdded ? 0.6 : 1};

    background: #FFFFFF;
    box-shadow: 0px 0px 30px rgba(51, 1, 109, 0.1);
    border-radius: 8px;

    @media screen and (min-width: 991px) {
      margin: 1rem 1rem 1rem 0rem;
    }
  `;
  return (<>
    <h2 className='mb-5'>Конфигурирай</h2>
    <div className='row'>
      {extras.map(({ extra, isAdded, isIncluded }, index) => (
        <SelectCard key={index} isAdded={isIncluded || isAdded}>
          <div className='row p-0 m-0'>
            <div className='col-12 col-md-8'>
              <div className='d-inline-block my-1'>
                {extra.icons.map((icon) => (
                  <img src={icon} className='mr-1 d-inline' height='22px' width='auto' alt='Екстра' />
                ))}
              </div>
              <h3 className='mt-1'>{extra.name}</h3>
              <p>{extra.description}</p>
            </div>
            <div className={'col-12 col-md-4 d-flex flex-column ' + (isIncluded ? 'justify-content-end' : 'justify-content-between')}>

              {!isIncluded && <PriceTag price={extra.price} />}

              <button
                disabled={isIncluded}
                className={'btn btn-primary no-min-width ' + (isIncluded ? 'dark-blue' : isAdded ? 'dark-blue' : 'blue')}
                onClick={() => (dispatch({
                  type: 'add_extra',
                  index: index,
                  isAdded: !isAdded
                }))}>
                {isIncluded ? 'Включено' : isAdded ? 'Премахни' : 'Добави'}
              </button>
            </div>
          </div>
        </SelectCard>))}
      <button className='btn btn-primary green mt-5' onClick={(e) => {
        e.preventDefault();
        dispatch({ type: 'next_step' });
      }}>Следваща стъпка</button>
    </div>
  </>);
};
